@charset "UTF-8";
/* Estilos específicos da página de contato */
.page-template-page-contato .contact-section {
  padding: 4rem 0;
}
.page-template-page-contato .contact-info {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .page-template-page-contato .contact-info {
    margin-bottom: 0;
  }
}
.page-template-page-contato .contact-info h2 {
  color: #800020;
  margin-bottom: 1.5rem;
}
.page-template-page-contato .contact-info .info-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1rem;
}
.page-template-page-contato .contact-info .info-item .icon {
  font-size: 1.5rem;
  color: #ffd700;
  margin-right: 1rem;
  width: 30px;
  text-align: center;
  flex-shrink: 0;
}
.page-template-page-contato .contact-info .info-item .details p {
  margin-bottom: 0.25rem;
}
.page-template-page-contato .contact-info .info-item .details a {
  color: #333333;
  text-decoration: none;
}
.page-template-page-contato .contact-info .info-item .details a:hover {
  color: #800020;
  text-decoration: underline;
}
.page-template-page-contato .contact-form {
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.page-template-page-contato .contact-form h2 {
  color: #800020;
  margin-bottom: 1.5rem;
  text-align: center;
}
.page-template-page-contato .contact-form .form-group {
  margin-bottom: 1.5rem;
}
.page-template-page-contato .contact-form label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #4a4a4a;
}
.page-template-page-contato .contact-form input[type=text],
.page-template-page-contato .contact-form input[type=email],
.page-template-page-contato .contact-form input[type=tel],
.page-template-page-contato .contact-form textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid #ebebeb;
  border-radius: 5px;
  transition: border-color 0.3s ease;
}
.page-template-page-contato .contact-form input[type=text]:focus,
.page-template-page-contato .contact-form input[type=email]:focus,
.page-template-page-contato .contact-form input[type=tel]:focus,
.page-template-page-contato .contact-form textarea:focus {
  border-color: #800020;
  outline: none;
}
.page-template-page-contato .contact-form textarea {
  min-height: 150px;
  resize: vertical;
}
.page-template-page-contato .contact-form .wpcf7-response-output {
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 5px;
}
.page-template-page-contato .contact-form .wpcf7-spinner {
  margin-left: 0.5rem;
}
.page-template-page-contato .contact-form button[type=submit] {
  width: 100%;
  padding: 1rem;
  font-size: 1.1rem;
  margin-top: 1rem;
}
.page-template-page-contato .map-section {
  margin-top: 4rem;
}
.page-template-page-contato .map-section iframe {
  display: block;
  width: 100%;
  height: 400px;
  border: none;
  border-radius: 10px;
}